export class Chandelier {

	timeline: Timeline;
	StrokeDashArray: StrokeDashArray;
	el: any;
	shapeArr = [];

	constructor(el) {
		this.initShapes();
		this.el = el;

		this.timeline = new Timeline();
		this.shapeArr.forEach((e, i) => {
			this.createShape(e.name, e.stroke, e.path, i, e.round);
		});
		this.createAmpersand();

		this.timeline.play();
	}

	createShape(name, stroke, path, index, round) {
		let angle = ['rand(-50,-70)'];
		let repeatCount = 0;
		let rPositions = [[-100, 100, 50], [-75, 75, 25], [-50, 50, 20], [-30, 30, 20], [-10, 10, 10], [0, 0]];

		

		let mql = window.matchMedia('(max-width:499px)').matches
		let height = window.innerHeight;
		if (mql) {
			height = window.innerHeight - 50;
		}

		let lPos = -100;
		let flip1 = false;
		let s = new Shape({
			shape: name,
			className: 'chandelier-shape',
			isShowEnd: true,
			isShowStart: true,
			parent: this.el,
			strokeLinecap: round ? 'round' : null,
			fill: 'white',
			strokeWidth: stroke,
			scale: 0.7,
			top: 0,
			origin: '50% 0%',
			left: '50%',
			angle: -10,
			x: 0,
			y: window.innerHeight + 280,
			repeat: 0,
			isYoyo: true,
			duration: 0,
			width: 286,
			height: 264,
		}).then({
			// delay: 7000,
			duration: 4000,
			easing: easing.ease.inout,
			angle: { '-40': 50 },
			y: { to: 180 }
		}).then({
			duration: 1200,
			easing: easing.cubic.inout,
			angle: { to: -30 },
		}).then({
			duration: 1200,
			easing: easing.cubic.inout,
			angle: { to: 20 },
		}).then({
			duration: 1200,
			easing: easing.cubic.inout,
			angle: { to: -10 },
		}).then({
			duration: 1200,
			angle: { to: 0 },
		});
		this.timeline.add(s);
	}

	initShapes() {
		addShape('ChandlierShape', ChandlierShape);
		addShape('Ampersand', Ampersand);

		this.shapeArr.push(
			{
				name: 'ChandlierShape',
				stroke: 1, round: true,
				path: null
			},
		)
	}

	createAmpersand() {
		let s = new Shape({
			shape: 'Ampersand',
			fill: 'white',
			stroke: 'none',
			parent: this.el,
			fillOpacity: { 0: 1 },
			top: 140,
			origin: '50% -180px',
			left: '50%',
			easing: easing.cubic.inout,
			duration: 0,
			width: 412,
			height: 203,
			y: window.innerHeight + 280,
			x: -16,
			scale: 1,
			isShowStart: true,
		}).then({
			duration: 4000,
			easing: easing.ease.inout,
			angle: { '-40': 50 },
			y: { to: 185 }
		}).then({
			duration: 1200,
			easing: easing.cubic.inout,
			angle: { to: -30 },
		}).then({
			duration: 1200,
			easing: easing.cubic.inout,
			angle: { to: 20 },
		}).then({
			duration: 1200,
			easing: easing.cubic.inout,
			angle: { to: -10 },
		})
			.then({
				easing: easing.cubic.inout,
				duration: 500,
				angle: { to: 0 },
				top: window.innerHeight / 2,
				x: { to: 0 },
				y: { to: -5 }
			})
		this.timeline.add(s);
	}
}

